@media (max-width: 767px) {
  h2 {
    padding-top: 20px;
    padding-bottom: 10px;
    font-size: 20px;
  }
}

@media (min-width: 767px) and (max-width: 991px) {
  h2 {
    padding-top: 30px;
    padding-bottom: 20px;
    font-size: 26px;
  }
}

@media (min-width: 991px) {
  h2 {
    padding-top: 40px;
    padding-bottom: 30px;
    font-size: 32px;
  }
}

.somos {
  padding-top: 20px;
  padding-bottom: 20px;
}

.somos-impar {
  background-color: #f8f8f8;
}

.row.is-flex > .somos-img {
  text-align: center;
}

@media (min-width: 767px) {
  .row.is-flex > .somos-img {
    display: block;
  }
}

@media (max-width: 767px) {
  .somos-img img {
    height: 80px;
    margin-bottom: 20px;
  }
}

@media (min-width: 767px) and (max-width: 991px) {
  .somos-img img {
    height: 120px;
  }
}

@media (min-width: 991px) {
  .somos-img img {
    height: 160px;
  }
}

@media (min-width: 767px) {
  .row.is-flex > .somos-txt {
    justify-content: center;
  }
}

.somos-txt {
  font-weight: 300;
  text-align: justify;
}

@media (max-width: 767px) {
  .somos-txt p {
    font-size: 14px;
  }
}

@media (min-width: 767px) and (max-width: 991px) {
  .somos-txt p {
    font-size: 18px;
  }
}

@media (min-width: 991px) {
  .somos-txt p {
    font-size: 24px;
  }
}
